'use strict';
const  test = require('ava');
const mimic = require('./index');

test('main', t => {
	

    const sayCheese = {
        say : "Cheese"
    };
    
    
    function wrap() {
        return sayCheese;
    }
    mimic(wrap, sayCheese);
    console.log(sayCheese.say);

    //t.is(wrap.name, sayCheese.name);
    t.is(wrap.say, sayCheese.say);

	
});